@import url(//db.onlinewebfonts.com/c/d9e24a306397c4e3cad3a8707c535715?family=AinslieW01-CondRegular);
@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap');
$backgroundButton: #ff268b;

.searchIcon {
    color: #ffff;
}

button {
    background-color: transparent;
    outline: none;
    border: none;
    transition: 0.5s;
}

.header {
    height: 783px;
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: #f7f4fb;
    flex-wrap: wrap;

    .wrapContact {
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            font-size: 11px;
            padding: 0 7px;
            border-right: 1px solid #3a364e;
            transition: 0.4s;
            color: #3a364e;
            font-weight: bold;
            text-decoration: none;
        }
        a:hover {
            border-bottom: 1px solid black;
        }
    }

    .wrapNav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 55px 0 35px 0;

        .logo {
            height: 25px;
            width: auto;
        }

        .nav {
            a {
                margin-right: 37px;
                font-size: 15px;
                color: #3a364e;
                font-weight: bold;
                text-decoration: none;
                transition: 0.4s;
            }
            a:hover {
                text-decoration: underline;
            }

            .iconButton {
                margin-right: 30px;
                transition: 0.4s;
                icon-icon {
                    transition: 0.5s;
                }
            }
            .iconButton:hover {
                box-shadow: 1px 1px -5px 8px black;
                ion-icon {
                    transform: scale(1.2);
                    box-shadow: -7px 10px 42px -16px rgba(0, 0, 0, 0.75);
                }
            }
        }
    }

    .wrapDriving {
        display: flex;
        position: relative;
        width: 95%;
        .text {
            .wrapLabel {
                position: relative;
                .line {
                    position: absolute;
                    bottom: 30px;
                    z-index: 0;
                }
                .hiddenOnMobile {
                    right: 50px;
                }
                h2 {
                    font-size: 55px;
                    font-weight: 400;
                    color: #3a364e;
                    line-height: 1.3;
                    z-index: 1;
                }
            }

            h3 {
                font-size: 18px;
                color: #9a9a9a;
                margin-bottom: 35px;
            }

            button {
                font-size: 15px;
                font-weight: bold;
                padding: 0;
                transition: 0.4s;
                font-family: 'Montserrat Alternates', sans-serif;
                transition: 0.5s;
            }
            button:hover {
                text-decoration: underline;
                transform: scale(1.01);
            }
        }

        .girl {
            height: 550px;
            width: auto;
            z-index: 1;
        }
        .patternHeader {
            position: absolute;
            z-index: 0;
            right: 0px;
            width: 450px;
            height: auto;
        }
    }
}

.contactUs {
    width: 150px;
    height: 45px;
    font-size: 15px;
    color: #ffff;
    transition: 0.5s;
    background-color: $backgroundButton;
    font-family: 'Titillium Web', sans-serif;
}

.contactUs:hover {
    background-color: transparent;
    color: $backgroundButton;
    border: 1px solid $backgroundButton;
    font-weight: bold;
    letter-spacing: 1px;
    box-shadow: -7px 10px 42px -13px $backgroundButton;
    border-radius: 5px;
}

.ant-badge-count-sm {
    min-width: 10px;
    height: 10px;
    font-size: 8px;
}

.ant-drawer-body {
    display: flex;
    flex-direction: column;
    a {
        margin-right: 37px;
        font-size: 15px;
        color: #3a364e;
        font-weight: bold;
        text-decoration: none;
        transition: 0.4s;
        margin: 10px 0;
        padding: 5px;
    }
    .contactUs {
        width: 100%;
    }
}

.ant-drawer-header {
    display: flex;
    justify-content: center;
    font-size: 17px;
    font-weight: bold;
}

@media screen and (max-width: 756px) {
    .header {
        .wrapNav {
            .nav {
                display: none;
            }
            .buttonMenu {
                font-size: 18px;
            }
        }
        .wrapDriving {
            width: 100%;
            .girl {
                display: none;
            }

            .text {
                width: 100%;
                .wrapLabel {
                    position: relative;
                    display: flex;
                    justify-content: center;
                    .hiddenOnMobile {
                        display: none;
                    }

                    .line {
                        bottom: 10px;
                    }
                    h2 {
                        font-size: 35px;
                        text-align: center;
                    }
                }
                h3 {
                    font-size: 16px;
                    text-align: center;
                }

                button {
                    margin: 0 auto;
                }
                .wrapButton {
                    justify-content: center;
                    display: flex;
                    align-items: flex-start;
                    .wrapLatestProject {
                        margin: 0 auto;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 756px) {
    .header {
        .wrapNav {
            .nav {
                display: none;
            }
            .buttonMenu {
                font-size: 18px;
            }
        }

        .showOnMobile {
            display: inline;
        }
        .wrapDriving {
            width: 100%;
            .girl {
                display: none;
            }

            .text {
                width: 100%;
                .wrapLabel {
                    position: relative;
                    display: flex;

                    .hiddenOnMobile {
                        display: flex;
                    }
                }

                .wrapButton {
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                    .wrapLatestProject {
                        margin: 0;
                    }
                }
            }
        }
    }
}
@media screen and (min-width: 992px) {
    .ant-drawer-content-wrapper {
        display: none;
    }
    .header {
        .wrapNav {
            .nav {
                display: flex;
            }
            .buttonMenu {
                font-size: 18px;
            }
        }

        .showOnMobile {
            display: none;
        }
        .wrapDriving {
            width: 95%;
            align-items: center;
            .girl {
                display: flex;
            }

            .text {
                width: 100%;
                .wrapLabel {
                    position: relative;
                    display: flex;
                    h2 {
                        font-size: 50px;
                        width: 90%;
                    }

                    .hiddenOnMobile {
                        display: flex;
                    }
                }

                h3 {
                    font-size: 18px;
                }

                .wrapButton {
                    display: flex;
                    justify-content: flex-start;
                }
            }
        }
    }
}
